import { HttpClient, HttpHeaders} from '@angular/common/http'
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/timeout';

@Injectable()
export class ZipCodeProvider {
  apikey = 'CFEMFB9IIZ21KmsYUOJxxkAZM3sbIc9uFM7QluCMtrtBTrztqq9pdRd7zekTR2Fu';

  url = 'https://www.zipcodeapi.com/rest/CFEMFB9IIZ21KmsYUOJxxkAZM3sbIc9uFM7QluCMtrtBTrztqq9pdRd7zekTR2Fu/info.json/92126/degrees';

  constructor(private http: HttpClient) {
    console.log('Hello ZipCodeProvider Provider');
  }

  getCityByZipCode() {
    var res = this.http.get(this.url);
    return res;
  }


}



